html { scroll-behavior: smooth; } 

.middle {
  width: 180px;
  height: 60px;
  position: relative;
}

.middle a{
    text-decoration: none;
    display: flex;
    flex-direction: column;
    justify-content: center;    
    text-align: center;
}

.btn {
    
    width: 180px;
    height: 60px;
    cursor: pointer;
    background: transparent;
    border: 1px solid #000000;
    outline: none;
    transition: 1s ease-in-out;
}

svg {
  position: absolute;
  left: 0;
  top: 0;
  fill: none;
  stroke: #fff;
  stroke-dasharray: 150 480;
  stroke-dashoffset: 150;
  transition: 1s ease-in-out;
}

.btn:hover {
  transition: 1s ease-in-out;
  background: rgb(39, 179, 132);
  color: #fff;
}

.btn:hover svg {
  stroke-dashoffset: -480;
  color: #fff;
}

.btn span {
  color: rgb(0, 0, 0);
  font-size: 18px;
  font-weight: 100;
}
.btn span:hover {
    color: #fff;
}




